import React, { Component } from 'react';
import cls from './styles.module.scss';
import UniRow from './UniRow';
import classnames from 'classnames';
import {
    UniRContainerVertical,
    UniRPageLayout,
    UniRButton,
    UniRContainerHorizontal,
    UniRWidget,
    UniRPanel
} from '../../common';
import {
    Input,
    Dropdown,
    Button,
    Menu,
    Icon
} from 'antd';

const images = [
    require('../../assets/slideshow/1.jpg'),
    require('../../assets/slideshow/2.jpg'),
    require('../../assets/slideshow/3.jpg'),
];

const uniData = [
    {
        logo: require('../../assets/uni_logo/fpt.png'),
        title: "Trường đại học FPT",
        shortDescription: `Chính thức thành lập ngày 8/9/2006 theo Quyết định của Thủ tướng Chính phủ,
        Trường Đại học FPT trở thành trường đại học đầu tiên của Việt Nam do một doanh nghiệp đứng
        ra thành lập với 100% vốn đầu tư từ Tập đoàn FPT.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/rmit.png'),
        title: "Trường đại học RMIT",
        shortDescription: `Tại RMIT Việt Nam, bạn sẽ được hòa mình và trải nghiệm một nền
        giáo dục được thiết kế để dẫn lối thành công, giúp trang bị những kỹ năng và tri thức cho
         bạn tiến xa trên con đường sự nghiệp đã chọn.
        Trường cung cấp đa dạng các dịch vụ hỗ trợ sinh viên và hệ thống cơ sở vật chất đẳng cấp.
        Hãy gia nhập cộng đồng năng động, thân thiện tại RMIT và tận hưởng hàng loạt những hoạt động xã hội, văn hóa, thể thao và giải trí
        đầy màu sắc tùy theo sở thích của bạn.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/tlu.jpg'),
        title: "Trường đại học Thăng Long",
        shortDescription: `Trường Đại học Thăng Long là một cơ sở kinh doanh dịch vụ giáo dục tại Việt Nam,
        là cơ sở giáo dục bậc đại học ngoài công lập đầu tiên trong
        chính thể Cộng hòa xã hội chủ nghĩa Việt Nam, được thành lập năm 1988.`,
        mark: 21
    },
]

class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: null
        };
    }

    handleClick = (e) => {
        this.setState({ selected: { key: e.key, value: e.item.props.children } })
    }

    render() {
        const menu = (
            <Menu onClick={this.handleClick}>
                <Menu.Item key="0">Tất cả</Menu.Item>
                <Menu.Item key="1">Công nghệ thông tin</Menu.Item>
                <Menu.Item key="2">Thiết kế đồ họa</Menu.Item>
                <Menu.Item key="3">Kinh doanh quốc tế</Menu.Item>
            </Menu >
        );

        let dropdownPlacholder =
            this.state.selected !== null ?
                this.state.selected.value :
                "Tất cả";

        let unis = [];
        uniData.map((uni, i) => (
            unis.push(
                <UniRow
                    key={i}
                    data={uni}
                />
            )
        ))
        return (
            <UniRContainerVertical>
                <UniRPageLayout imagesSlideShow={images}>
                    <UniRContainerHorizontal>
                        <div className={classnames("col-7")}>
                            <Input
                                allowClear
                                placeholder="Nhập tên trường đại học"
                                size="large"
                            />
                        </div>
                        <div className={classnames(cls.dropdown, "col-4")}>
                            <Dropdown overlay={menu}>
                                <Button size="large">
                                    {dropdownPlacholder}
                                    <span>
                                        <Icon type="down" />
                                    </span>
                                </Button>
                            </Dropdown>
                        </div>
                        <div className={classnames("col-1")}>
                            <UniRButton
                                size="large"
                                icon="search"
                                loading={false}
                                onClick={() => { }}
                            />
                        </div>
                    </UniRContainerHorizontal>
                    <div className={cls.res_count}>
                        3 Results
                        </div>
                    <UniRPanel>
                        <UniRPanel.Header title="Test Header" />
                        <UniRPanel.Body>
                            {unis}
                        </UniRPanel.Body>
                    </UniRPanel>
                </UniRPageLayout>
            </UniRContainerVertical>
        );
    }
}

export default Search;