import React, { Component } from 'react';
import Img from 'react-image';
import cls from './styles.module.scss';
import UniRow from './UniRow';
import {
    UniRContainerVertical,
    UniRPageLayout,
    UniRButton,UniRContainerHorizontal, UniRImage
} from '../../common';
import classnames from 'classnames';
import MajorBox from './MajorBox';
import HomeBox from './HomeBox';
import BloxBox from './BlogBox';
import {
    Row,Col
} from 'antd';

import { connect } from "react-redux";
import {
    searchAct,
}
    from "../../actions/blog";
import BlogBox from './BlogBox';

const ROW_PER_PAGE = 4;

const images = [
    require('../../assets/slideshow/1.jpg'),
    require('../../assets/slideshow/2.jpg'),
    require('../../assets/slideshow/3.jpg'),
];
const uniData = [
    {
        logo: require('../../assets/uni_logo/fpt.png'),
        title: "Trường đại học FPT",
        shortDescription: `Chính thức thành lập ngày 8/9/2006 theo Quyết định của Thủ tướng Chính phủ,
        Trường Đại học FPT trở thành trường đại học đầu tiên của Việt Nam do một doanh nghiệp đứng
        ra thành lập với 100% vốn đầu tư từ Tập đoàn FPT.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/rmit.png'),
        title: "Trường đại học RMIT",
        shortDescription: `Tại RMIT Việt Nam, bạn sẽ được hòa mình và trải nghiệm một nền
        giáo dục được thiết kế để dẫn lối thành công, giúp trang bị những kỹ năng và tri thức cho
         bạn tiến xa trên con đường sự nghiệp đã chọn.
        Trường cung cấp đa dạng các dịch vụ hỗ trợ sinh viên và hệ thống cơ sở vật chất đẳng cấp.
        Hãy gia nhập cộng đồng năng động, thân thiện tại RMIT và tận hưởng hàng loạt những hoạt động xã hội, văn hóa, thể thao và giải trí
        đầy màu sắc tùy theo sở thích của bạn.`,
        mark: 21
    },
    {
        logo: require('../../assets/uni_logo/tlu.jpg'),
        title: "Trường đại học Thăng Long",
        shortDescription: `Trường Đại học Thăng Long là một cơ sở kinh doanh dịch vụ giáo dục tại Việt Nam,
        là cơ sở giáo dục bậc đại học ngoài công lập đầu tiên trong
        chính thể Cộng hòa xã hội chủ nghĩa Việt Nam, được thành lập năm 1988.`,
        mark: 21
    },
];

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs : []
        }
    }

    componentDidMount(){
        this.props.searchAct("", 0, ROW_PER_PAGE, (data) => {
            let d = JSON.parse(data);

            let blogs = JSON.parse(d.data);
        
            this.setState({
                blogs: blogs
            });
        })
    }

    render() {
        let unis = [];
        uniData.map((uni, i) => (
            unis.push(
                <UniRow
                    key={i}
                    data={uni}
                />
            )
        ));

        let blogs= this.state.blogs;
        
        let blogData =[];

        blogs.map((blog, i) =>(

            
            blogData.push(<BlogBox key={i} data = {
                
                Object.assign({}, blog, {
                    thumb: blog.ArticleImages,
                    title: blog.ArticleTitle,
                    shortDescription: "",
                    link:'https://www.google.com/'
                })

            } />)));

       
        return (
            <UniRContainerVertical>
                <UniRPageLayout imagesSlideShow={images}>
                    <HomeBox title="Trắc nghiệm chọn Đại học" position="center" className={classnames(cls.divClickToTest)}>
                            <div className={classnames(cls.body)}>
                                
                              <div>
                              <UniRImage 
                                src={require('../../assets/home/test.jpg')}
                                className={classnames(cls.avatar)}
                              />
                              </div>
                                
                                
                                <UniRButton size="large" type="primary" title={"Làm bài Test"} btnStyle={classnames(cls.button)}/> 
                            </div>
                    </HomeBox>
                    <HomeBox title="Khối ngành" position="left" className={classnames(cls.major)}>
                        <Row gutter={16} className={classnames(cls.majorRow)}>
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Kinh tế"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                            <MajorBox title="Công nghệ thông tin"
                                link="http://google.com.vn/" icon="code"
                            />
                        </Row>
                    </HomeBox>
                    <HomeBox title="Đại học nổi bật" position="right" className={classnames(cls.university)}>
                        {unis}
                    </HomeBox>  
                    <HomeBox title="Blog" position="left" className={classnames(cls.blog)}>
                        <Row gutter={16} className={classnames(cls.blogRow)}>
                            {blogData}
                        </Row>
                    </HomeBox>  
                </UniRPageLayout>
            </UniRContainerVertical>
        )
    }
}

const mapStateToProps = state => {
    return {
        blog: state.blog
    }
}

const mapDispatchToProps = dispatch => {
    return {
        searchAct: (whereClause, indexPage, ROW_PER_PAGE, callbackSuccess) => {
            dispatch(searchAct(whereClause, indexPage, ROW_PER_PAGE, callbackSuccess))
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home);


