    import React, { Component } from 'react';
    import cls from './styles.module.scss';
    import classnames from 'classnames';
    import Img from 'react-image';
    import TopUniColumn  from './TopUniColumn';
    import { 
        UniRPageLayout,
        UniRContainerVertical,
        UniRContainerHorizontal,
        UniRContainer,
        UniRWidget
     } from '../../../common';
     import {
         Icon
     } from 'antd';
     class TestResult extends Component{
        constructor(props) {
             super(props);
             this.state = {
                 value: "",
             }
        }
        
        render() {
            let characteristic = this.props.value;
            let fptLogo = require('../../../assets/uni_logo/fpt.png');
            let rmitLogo = require('../../../assets/uni_logo/rmit.png');
            let tluLogo = require('../../../assets/uni_logo/tlu.jpg');
            return (
                
                    <UniRContainerVertical >
                        <UniRContainerVertical className={classnames(cls.test_result_characteristic)}>
                            <div className={classnames(cls.characteristic_title)}>
                            Kết quả trắc nghiệm cho thấy, mã tính cách của bạn là
                            </div>
                            <UniRWidget primary className={classnames(cls.characteristic)} >
                            {characteristic}
                            </UniRWidget>
                        </UniRContainerVertical>
                        <UniRContainerVertical>
                            <UniRContainerHorizontal className={classnames(cls.char_detail)}>
                                <div className={classnames(cls.char_description_letter)}>
                                A
                                </div>
                                <div className={classnames(cls.char_description)}>
                                : Có khả năng về nghệ thuật, khả năng về trực giác, khả năng tưởng tượng cao, thích làm việc trong các môi trường 
                                    mang tính ngẫu hứng, không khuôn mẫu.
                                </div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={classnames(cls.career_recomendation)}>
                                <div className={classnames(cls.enter_icon)}>
                                    <Icon  type='enter'/>
                                </div>
                                <div className={classnames(cls.career_recomendation_description)}>
                                Ngành nghề phù hợp với nhóm này bao gồm: Các ngành về văn chương; báo chí (bình luận viên, dẫn chương 
                                trình...); điện ảnh, sân khấu, mỹ thuật, ca nhạc, múa, kiến trúc, thời trang, hội họa, giáo viên dạy sử/Anh văn, bảo 
                                tàng, bảo tồn...
                                </div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                        <UniRContainerVertical>
                            <UniRContainerHorizontal className={classnames(cls.char_detail)}>
                                <div className={classnames(cls.char_description_letter)}>
                                I
                                </div>
                                <div className={classnames(cls.char_description)}>
                                : Có khả năng về quan sát, khám phá, phân tích đánh giá và giải quyết các vấn đề.
                                </div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={classnames(cls.career_recomendation)}>
                                <div className={classnames(cls.enter_icon)}>
                                    <Icon  type='enter'/>
                                </div>
                                <div className={classnames(cls.career_recomendation_description)}>
                                Ngành nghề phù hợp với nhóm này bao gồm: Các ngành thuộc lĩnh vực khoa học tự nhiên (toán, lý, hóa, sinh, địa 
                                lý, địa chất, thống kê...); khoa học xã hội (nhân học, tâm lý, địa lý...); y - dược (bác sĩ gây mê, hồi sức, bác sĩ phẫu 
                                thuật, nha sĩ...); khoa học công nghệ (công nghệ thông tin, môi trường, điện, vật lý kỹ thuật, xây dựng...); 
                                nông lâm (nông học, thú y...).
                                </div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                        <UniRContainerVertical>
                            <UniRContainerHorizontal className={classnames(cls.char_detail)}>
                                <div className={classnames(cls.char_description_letter)}>
                                R
                                </div>
                                <div className={classnames(cls.char_description)}>
                                : Người thuộc nhóm sở thích nghề nghiệp này thường có khả năng về kỹ thuật, công nghệ, hệ thống; ưa thích làm 
                                    việc với đồ vật, máy móc, động thực vật; thích làm các công việc ngoài trời.
                                </div>
                            </UniRContainerHorizontal>
                            <UniRContainerHorizontal className={classnames(cls.career_recomendation)}>
                                <div className={classnames(cls.enter_icon)}>
                                    <Icon  type='enter'/>
                                </div>
                                <div className={classnames(cls.career_recomendation_description)}>
                                Ngành nghề phù hợp với nhóm này bao gồm những nghề về kiến trúc, an toàn lao động, nghề mộc, xây dựng, 
                                thủy sản, kỹ thuật, máy tàu thủy, lái xe, huấn luyện viên, nông - lâm nghiệp (quản lý trang trại, nhân giống cá, 
                                lâm nghiệp...), cơ khí (chế tạo máy, bảo trì và sửa chữa thiết bị, luyện kim, cơ khí ứng dụng, tự động...), điện - điện 
                                tử, địa lý - địa chất (đo đạc, vẽ bản đồ địa chính), dầu khí, hải dương học, quản lý công nghiệp...
                                </div>
                            </UniRContainerHorizontal>
                        </UniRContainerVertical>
                        <UniRContainerHorizontal className={classnames(cls.top_uni)}>
                            <TopUniColumn charName='Artistic - Nghệ thuật' ></TopUniColumn>
                            <TopUniColumn charName='Artistic - Nghệ thuật' ></TopUniColumn>
                            <TopUniColumn charName='Artistic - Nghệ thuật' noBorderRight></TopUniColumn>
                        </UniRContainerHorizontal>
                    </UniRContainerVertical>
            );
        }
     }

export default TestResult;